}

div.newsletterDiv{
    background-color: rgb(56, 60, 64);
    min-width: 400px;
    border-radius: 5px;
    padding: 12px;
}

.btnToSpread{
    width: 100%;
}
.rowCards{
    height: 14em;
}

.colSleep{
    background-color: inherit;
}

